.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 990;
    height: 100vh;
    overflow-x: scroll;
  }
  
  .modal .overlay {
    position: absolute;
    left: 0;
    overflow:scroll ;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 995;
    background:linear-gradient(rgb(238, 238, 238) 300px, rgb(255, 255, 255) 100%);;
  }
  
  .modal .modal_content {
    z-index: 999;
    position: absolute;
    top: 60%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    max-height: 90%;
    overflow: hidden;
    background: #fff;
    box-sizing: border-box;
    padding: 20px;
    border-radius: 8px;
    width: 70%; /* This just a default width */
  }
  
  .modal .modal_content > h2 {
    font-size: 28px;
    font-weight: 200;
    margin: 20px 0 40px;
    text-align: center;
  }
  
  .modal .modal_content .buttons_wrapper {
    padding: 20px;
  }
  
  .modal .close_modal {
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
    font-size: 18px;
    opacity: 0.5;
    background: none;
    border: none;
    transition: opacity 0.2s ease;
  }
  
  .modal .close_modal:hover {
    opacity: 0.9;
  }
